import React from 'react'
import ReactMarkdown from 'react-markdown'

import Box from '../../components/Box';
import Image from '../../components/Image';
import Flex from '../../components/Flex';
import Text from '../../components/Text';
import Link from '../../components/Link';
import useShowHeader from '../../contexts/header/useShowHeader';

import rethink from './rethink.svg'
import relab from './relab.svg'
import renato from './renato.svg'
import about from './about.svg'
import picture from './picture.svg'
import { responsive } from '../../components/ThemeProvider/theme';
import Footer from '../Footer';

const data = [
  {
    team: '',
    name: 'RE-THINK\n',
    intro: 'RE-THINK2013RE-THINK3\n[](https://oceantrash.rethinktw.org/)',
    logo: rethink
  },
  {
    team: '',
    name: 'Re-lab',
    intro: '60%\nRe-lab1100\nRe-lab',
    logo: relab
  },
  {
    team: '',
    name: 'REnato lab',
    intro: '2014REnato labREnato lab\nREnato lab',
    logo: renato
  },
]

const About = () => {
  useShowHeader('colors.yellow')

  return (
    <Box pt={responsive('10em', '3em')} bg="colors.yellow">
      <Box
        bg="colors.yellow"
        pt={responsive('3em', "2em")}
        pb="8.125em"
        px={responsive('5em', '10em')}
      >
        <Image src={about} />
        <Flex alignItems={responsive('auto', 'stretch')} flexDirection={responsive('column', 'row')}>
          <Box mr={responsive(0, '-1.25em')} width={responsive('50%', 1 / 2)}>
            <Image src={picture} />
          </Box>
          <Flex
            flex={1}
            pt={responsive('3em', '1em')}
            flexDirection="column"
            justifyContent="space-between"
          >
            <Box fontSize={responsive('3em', '1em')} pb="0.625em" letterSpacing="0.125em">
              <Text textAlign="justify"></Text>
              <Text textAlign="justify" mt="1em">3PLA</Text>
              <Text textAlign="justify" mt="1em"></Text>
            </Box>
            <Flex pt={responsive('2em', 0)} justifyContent="flex-end" alignItems="flex-end">
              <Text lineHeight="1" fontSize={responsive('3.5em', '0.9375em')}>RE-THINK</Text>
              <Text.Inline lineHeight="1" pl="0.3125em" fontSize={responsive('4.5em', '1.75em')}>
                
              </Text.Inline>
            </Flex>
          </Flex>
        </Flex>
      </Box>
      <Box bg="white" pt="5em">
        <Text letterSpacing="0.125em" fontWeight="900" px={responsive('3rem', '4.6875rem')} fontSize={responsive('2.5em', '2em')} color="yellow.500"></Text>
        {data.map(({ team, name, intro, logo }, i) => (
          <Flex
            flexDirection={responsive('column', 'row')}
            position="relative"
            py="4.6875em"
            px={responsive('5em', '10em')}
            alignItems="center"
            justifyContent="space-between"
            key={i}
          >
            <Box position="relative" width={responsive('35%', '9.375em')}>
              <Image src={logo} />
            </Box>
            <Box position="relative" pl={responsive(0, '6.875em')} pt={responsive('5em', 0)} flex={1}>
              <Text.Bold fontSize={responsive('3.5em', '1.25em')}>{team}</Text.Bold>
              <Text.Bold whiteSpace="pre-wrap" my="0.15625em" fontSize={responsive('4em', '1.71875em')}>{name}</Text.Bold>
              <Text
                lineHeight="1.75"
                whiteSpace="pre-wrap"
                letterSpacing="0.125em"
                fontSize={responsive('3em', '0.875em')}
                textAlign="justify"
              >
                <ReactMarkdown renderers={{ link: Link.MdLink }}>
                  {intro}
                </ReactMarkdown>
              </Text>
            </Box>
          </Flex>
        ))}
      </Box>
      {/* <Sponsor textColor="white" bgColor="colors.yellow" /> */}
      <Footer />
    </Box>
  )
}

export default About
